@import "_import";

.c-header {
  &__wrap {
    .c-header__keyword {
      transition: color 0.2s ease;
    }
    .c-header__menu-btn {
      transition: color 0.2s ease;
    }
    .c-header__logo-img {
      transition: opacity 0.2s ease;
    }
    .c-header__logo-img {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}
.discover {
  overflow: hidden;
  @at-root .br-ie & {
    overflow-x: hidden;
  }
  // background: blue;
  $self: &;
  &__sec {
    &-1 {
      position: relative;
      z-index: 3;
      width: 100%;
      background-color:#fff;
      .c-pin-target{
        height: 100vh;
      }
    }
    &-2 {
      position: relative;
      margin-top: -100vh;
      z-index: 2;
      background-color: #fff;
      overflow: hidden;
      .discover-visual{
        .discover-visual__txt--btm {
          @include mobile {
            bottom: 80rem;
          }
        }
        &__img{
          position: relative;
          padding-top: 125.2791%;
          img{
            position: absolute;
            top: 0;
            left: 0;
          }
          @include mobile{
            padding-top: 0;
            img{
              position: static;
            }
          }
        }
      }
      .cover-img{
        position: relative;
        height: 100vh;
        width: 100%;
        img{
          position: absolute;
          top: 0;
          left: 0;
        }
      }
      .discover-info-wrap{
        // padding-bottom: 340rem;
      }
      .discover-info__gallery{
        padding-top: 560rem;
        @include mobile {
          padding-top: 290rem;
        }
      }
      .discover-visual__txt-tit{
        font-size: 268rem;
        color: #fff;
        font-family: 'ambit-semi';
        overflow: hidden;
        letter-spacing: -6rem;
        em {
          font-size: 250rem;
          font-family: 'roslindaletext';
        }
        &.only-mo{
          .discover-visual__txt-ltrs--1{
            span{
              font-family: 'roslindaletext';
              font-size: 70rem;
            }
          }
        }
        @at-root .br-ie &{
          line-height: 1.1;
          font-size: 14vw;
          letter-spacing: -0.52vw;
          em{
            font-size: 13vw;
            letter-spacing: -0.26vw
          }
          @include full {
            font-size: 268px;
            letter-spacing: -10px;
            em {
              font-size: 250px;
              letter-spacing: -5px;
            }
          }
        }
        @include pc  {
          font-size: 238rem;
          letter-spacing: -8rem;
          em {
            font-size: 222rem;
            letter-spacing: -8rem;
          }
        }
        @include mobile  {
          font-size: 75rem;
          letter-spacing: 0;
          @include tablet {
            font-size: 133rem;
          }
          &--1 {
            font-family: 'roslindaletext';
            font-size: 70rem;
            .txt-type-ambit{
              font-family: 'ambit-semi';
            }
            @include tablet {
              font-size: 124rem;
            }
          }
        }
        &--type2 {
          font-size: 324rem;
          font-family: 'roslindaletext';
