      margin-top: -100vh;
      z-index: 1;
      overflow: hidden;
      background-color: #fff;
      @include mobile{
        padding-bottom: 100rem;
      }
      .discover-visual{
        &__img{
          position: relative;
          padding-top: 125%;
          img{
            position: absolute;
            top: 0;
            left: 0;
          }
