.c-voyage-copy{
  position: relative;
  width: 100%;
  height: 100%;
  &__tit {
    display: table;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) !important;
    font-family: 'ambit-semi';
    font-size: 360rem;
    @at-root .br-ie &{
      transform: translate(-50%, -50%) !important;
    }
    div{
      display: table-cell;
    }
    .v,.o{
      font-family: 'roslindaletext';
      font-size: 330rem;
      letter-spacing: -9rem;
      @include pc {
        font-size: 260rem;
      }
    }
    .y,.a{
      letter-spacing: -16rem;
    }
    .v{
      transform: translate(0, -65%);
    }
    .o{
      transform: translate(0, 100%);
    }
    .y{
      transform: translate(0, -18%);
      padding-left: 5px;
    }
    .a{
      transform: translate(0, 54%);
    }
    .g{
      transform: translate(0, -110%);
    }
    .e{
      transform: translate(0, -3%);
    }
    @include full {
      font-size: 360px;
    }
    @include pc {
      font-size: 290rem;
    }
    @include mobile{
      margin: 0 auto;
      font-size: 87rem;
      margin-top: -30rem;
      @include tablet{
        font-size: 151rem;
        margin-top:-60rem;
      }
      div{
        letter-spacing: 0;
      }
      .v,.o{
        font-size:80rem;
        letter-spacing: -4rem;
        @include tablet{
          font-size: 139rem;
        }
      }
      .v{
        transform: translate(3%, -37%);
      }
      .o{
        transform: translate(4%, 241%);
      }
      .y{
        letter-spacing: -5rem;
        transform: translate(5%, 30%);
      }
      .a{
        letter-spacing: -2rem;
        transform: translate(2%, 128%);
      }
      .g{
        transform: translate(2%, -120%);
      }
      .e{
        transform: translate(3%, 40%);
      }
    }
  }
  &__txt {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 120%);
    font-family: "Noto Sans KR Medium";
    font-size: 28rem;
    line-height: 1.5;
    text-align: center;
    @include mobile{
      width: 100%;
      font-family: "Noto Sans KR Medium";
      font-size: 18rem;
      line-height: 26rem;
      letter-spacing: 0.4rem;
      transform: translate(-50%, 20%);
    }
  }
}
.c-voyage-gallery{
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  &__list {
    height: 100%;
  }
  &__img-box{
    position: absolute;
    top: 100%;
    left: 50%;
    &1{
      width: 16.3%;
      transform: translate(143%, 0%);
    }
    &2{
      width: 24.42%;
      transform: translate(-165%, 0%);
    }
    &3{
      width: 26.19%;
      transform: translate(-39.5%, 0%);
    }
    &4{
      width: 36.71%;
      transform: translate(-243%, 0%);
    }
    &5{
      width: 16.3%;
      transform: translate(-21%, 0%);
    }
    @include mobile{
      &1{
        width: 30%;
        transform: translate(52%, 0%);
      }
      &2{
        width: 45%;
        left: 0;
        transform: translate(0%, 0%);
      }
      &3{
        width: 67.06%;
        transform: translate(-50%, 0%);
      }
      &4{
        width: 75.06%;
        left: auto;
        right: 0;
        transform: translate(0%, 0%);
      }
      &5{
        left: 4%;
        width: 33.33%;
        transform: translate(0%, 0%);
      }
    }
  }
}
.c-dimmed{
 position: fixed;
 top: 0;
 left: 0;
 width: 100vw;
 height: 100vh;
 background-color: #000;
 opacity: 0.9;
}
.quick {
  // mix-blend-mode: difference;
  $self: &;
  @include mobile{
    left: 16rem;
    right: 16rem;
    bottom: 15rem;
  }
  &__nav{
    opacity: 1;
    position: fixed;
    left: 50%;
    bottom: 40rem;
    text-align:center;
    background: #fff;
    border: 2px solid #000;
    border-radius: 80px;
    z-index: 10;
    transform: translateX(-50%);
    transition: opacity 0.3s 0.8s;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.15);
    @include mobile {
      border-width: 2px;
    }
    a{
      display: inline-block;
      padding: 14px 25px 14px 20px;
      font-family: 'ambit-semi';
      font-size: 32px;
      line-height: 1;
      color: #000;
      @at-root .br-ie &{
        padding: 17px 25px 9px 20px;
      }
      @at-root .br-win-ch &{
        padding: 16px 25px 12px 20px;
        @include mobile {
          padding: 8px 21px 6px 17px;
        }
      }
      @include mobile{
        padding: 7px 21px 7px 17px;
        font-size: 18px;
        vertical-align: middle;
        @at-root .br-safari & {
          padding: 8px 21px 6px 17px;
        }
      }
    }
  }
  &__list {
    opacity: 0;
    position: fixed;
    left: 50%;
    bottom: 40rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 19px 15px 20px;
    line-height: 1;
    vertical-align: middle;
    background: #000;
    border-radius: 80px;
    transform: translateX(-50%);
    z-index: 9;
    overflow: hidden;
    transition: opacity 0.3s .8s, width 0.8s 0s;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.15);
    @at-root .br-ie & {
      padding: 15px 15px 16px;
    }
    @at-root .br-win-ch & {
      padding: 17px 15px 19px;
      @include mobile {
        padding: 11px 14px;
      }
    }
    @include mobile{
      padding: 11px 14px;
      line-height: 1;
      transition: none;
      @at-root .br-safari & {
        padding: 12px 14px 10px;
      }
    }
  }
  &__item {
    opacity: 0;
    margin: 0 15px;
    font-size: 20px;
    font-family: 'ambit-semi';
    transition: opacity 0.2s .3s;
    &-btn {
      color:#fff;
      &--active {
        font-family: 'roslindaletext';
        font-size: 19px;
        color:#66c970;
        @at-root .br-win-ch & {
          padding: 3px 0 0;
        }
      }
    }
    @at-root .br-ie &{
      margin-top : 8px;
      &-btn {
        &--active {
          margin-top: -3px;
        }
      }
    }
    @at-root .br-win-ch & {
      &-btn:not(#{$self}__item-btn--active) {
        padding: 4px 0 0;
      }
    }
    @include full {
      font-size: 20px;
    }
    @include mobile{
      margin: 0 8rem;
      float: left;
      font-size: 10px;
      font-weight: bold;
      line-height: 1;
      &-btn {
        letter-spacing: 0.6px;
        margin-top:-4px;
        &--active {
          font-size: 10px;
        }
      }
    }
  }
  &--hide{
    opacity: 0 !important;
  }
  &--active{
    #{$self}__nav{
      opacity: 0;
      width: 100% !important;
      transition: opacity 0.3s, width 2s;
      @include mobile{
        transition: opacity 0.3s, width 1s;
      }
    }
    #{$self}__list{
      z-index: 10;
      width: 100% !important;
      opacity: 1;
      transition: opacity 0.3s, width 2s;
      @include mobile{
        transition: opacity 0.3s, width 1s;
      }
    }
    #{$self}__item{
      opacity: 1;
      transition: opacity 0.5s 0.5s;
      @include mobile{
        transition: opacity 0.5s;
      }
    }
  }
}
